body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {grid-area:logo;}
.rank {grid-area:rank;}
.signout {grid-area:signout;}
.faceDetect {grid-area:faceDetect;}

.grid-container {
  display: grid;
  grid-template-areas: 
  "logo rank rank signout"
  "faceDetect faceDetect faceDetect faceDetect";
  grid-gap: 10px;
}
.imgContainer {
	position: relative;
	display: inline-block;
}

.boundingBox {
	position: absolute; 
	display:none;
}

.boundingBox.active {
	position: absolute; 
	display:block;
	border: 4px solid blue;
}

#imgFace {
	width: 400px;
	height: auto;
}
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: auto;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 5px;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}
li {
	list-style: none;
}
